@use "colors";

.post_summary{
	margin-bottom: 1em;
	position: relative;
	&.realisations{
		.corner-ribbon{
			background-color: colors.$dark_teal-color;
			&:before{
				content: "RÃ©alisation"
			}
		}
		h2{
			color: colors.$dark_teal-color;
		}
		.chip{
			background: colors.$dark_teal-color;
		}
	}
	.corner-ribbon-wrapper {
		width: 90px;
		height: 90px;
		overflow: hidden;
		position: absolute;
		top: -3px;
		right: -3px;
		.corner-ribbon {
		 font: bold 12px sans-serif;
		 color: #333;
		 text-align: center;
		 -webkit-transform: rotate(45deg);
		 -moz-transform: rotate(45deg);
		 -ms-transform: rotate(45deg);
		 -o-transform: rotate(45deg);
		 position: relative;
		 padding: 2px 0;
		 top: 20px;
		 right: 0px;
		 width: 120px;
		 
		 color: #fff;
		}
		.blog{
			background-color: colors.$light_teal-color;
			&:before{
				content: "Article"
			}
		}
	}
	h2{
		margin-top: 0;
		width:100%;
		width: calc(100% - 40px);
	}
	.description{
		padding: 1em;
	}
	a{
		text-decoration: none;
		p {
			color: colors.$dark_teal-color;
		}
	}
	.thumbnail{
		max-width: 100%;
		max-height: 200px;
	}	
}

@media (prefers-color-scheme: dark) {
	.post_summary{
		a{
			p {
				color: colors.$dark_white-color;
			}
		}
	}
}